"use client";

import { useCallback, useEffect, useState } from "react";
import { useDashboardStore } from "../stores/dashboard-store";
import {
  dashboardApi,
  enhancedAgentsApi,
  type DashboardMetrics,
  type Conversation,
  type SystemStatus,
} from "../api/dashboard-api";
import type { Agent as ApiAgent, CreateAgentRequest } from "../api/agents-api";
import type { Agent as DashboardAgent } from "../types";

// Convert API Agent to Dashboard Agent
function convertApiAgentToDashboard(apiAgent: ApiAgent): DashboardAgent {
  return {
    id: apiAgent.id,
    name: apiAgent.name,
    biography: apiAgent.metadata?.biography as string | undefined,
    class: apiAgent.activeInference?.template,
    avatar: apiAgent.metadata?.avatar as string | undefined,
    inConversation: apiAgent.status === "interacting",
    position: apiAgent.position,
    color: (apiAgent.metadata?.color as string) || "#4A90E2",
    knowledge: [],
    autonomyEnabled: apiAgent.capabilities?.includes("autonomous") || false,
    toolPermissions: apiAgent.metadata?.toolPermissions,
  };
}

// Hook for dashboard metrics
export function useDashboardMetrics() {
  const [metrics, setMetrics] = useState<DashboardMetrics | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchMetrics = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const result = await dashboardApi.getMetrics();
      setMetrics(result);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to fetch metrics");
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchMetrics();
    // Refresh metrics every 30 seconds
    const interval = setInterval(fetchMetrics, 30000);
    return () => clearInterval(interval);
  }, [fetchMetrics]);

  return { metrics, loading, error, refetch: fetchMetrics };
}

// Hook for agents with Zustand integration
export function useAgents() {
  const { agents, setAgents } = useDashboardStore();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const fetchAgents = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const result = await enhancedAgentsApi.getAgents();
      setAgents(result.agents.map(convertApiAgentToDashboard));
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to fetch agents");
    } finally {
      setLoading(false);
    }
  }, [setAgents]);

  const createAgent = useCallback(async (request: CreateAgentRequest) => {
    try {
      setError(null);
      const result = await enhancedAgentsApi.createAgent(request);
      // Optimistically update store
      const dashboardAgent = convertApiAgentToDashboard(result.agent);
      useDashboardStore.getState().addAgent(dashboardAgent);
      return result.agent;
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to create agent");
      throw err;
    }
  }, []);

  const createAgentFromTemplate = useCallback(
    async (
      templateConfig: Parameters<
        typeof enhancedAgentsApi.createAgentFromTemplate
      >[0],
    ) => {
      try {
        setError(null);
        const result =
          await enhancedAgentsApi.createAgentFromTemplate(templateConfig);
        // Optimistically update store
        const dashboardAgent = convertApiAgentToDashboard(result.agent);
        useDashboardStore.getState().addAgent(dashboardAgent);
        return result.agent;
      } catch (err) {
        setError(
          err instanceof Error
            ? err.message
            : "Failed to create agent from template",
        );
        throw err;
      }
    },
    [],
  );

  const updateAgent = useCallback(
    async (agentId: string, updates: Partial<DashboardAgent>) => {
      try {
        setError(null);
        // Optimistically update store
        useDashboardStore.getState().updateAgent(agentId, updates);
        const result = await enhancedAgentsApi.updateAgent(agentId, updates);
        // Update with server response
        useDashboardStore.getState().updateAgent(agentId, result.agent);
        return result.agent;
      } catch (err) {
        // Revert optimistic update on error
        fetchAgents();
        setError(err instanceof Error ? err.message : "Failed to update agent");
        throw err;
      }
    },
    [fetchAgents],
  );

  const deleteAgent = useCallback(
    async (agentId: string) => {
      try {
        setError(null);
        // Optimistically update store
        useDashboardStore.getState().removeAgent(agentId);
        await enhancedAgentsApi.deleteAgent(agentId);
      } catch (err) {
        // Revert optimistic update on error
        fetchAgents();
        setError(err instanceof Error ? err.message : "Failed to delete agent");
        throw err;
      }
    },
    [fetchAgents],
  );

  const updateAgentPosition = useCallback(
    async (agentId: string, position: { x: number; y: number; z?: number }) => {
      try {
        setError(null);
        // Optimistically update store
        useDashboardStore.getState().updateAgentPosition(agentId, position);
        await enhancedAgentsApi.updateAgentPosition(agentId, position);
      } catch (err) {
        // Revert optimistic update on error
        fetchAgents();
        setError(
          err instanceof Error
            ? err.message
            : "Failed to update agent position",
        );
        throw err;
      }
    },
    [fetchAgents],
  );

  const toggleAgentAutonomy = useCallback(
    async (agentId: string, enabled: boolean) => {
      try {
        setError(null);
        // Optimistically update store
        useDashboardStore.getState().toggleAgentAutonomy(agentId, enabled);
        await enhancedAgentsApi.toggleAgentAutonomy(agentId, enabled);
      } catch (err) {
        // Revert optimistic update on error
        fetchAgents();
        setError(
          err instanceof Error
            ? err.message
            : "Failed to toggle agent autonomy",
        );
        throw err;
      }
    },
    [fetchAgents],
  );

  useEffect(() => {
    if (agents.length === 0) {
      fetchAgents();
    }
  }, [agents.length, fetchAgents]);

  return {
    agents,
    loading,
    error,
    refetch: fetchAgents,
    createAgent,
    createAgentFromTemplate,
    updateAgent,
    deleteAgent,
    updateAgentPosition,
    toggleAgentAutonomy,
  };
}

// Hook for conversations
export function useConversations() {
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const fetchConversations = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const result = await dashboardApi.getConversations();
      setConversations(result.conversations);
    } catch (err) {
      setError(
        err instanceof Error ? err.message : "Failed to fetch conversations",
      );
    } finally {
      setLoading(false);
    }
  }, []);

  const startConversation = useCallback(
    async (agentIds: string[], topic?: string) => {
      try {
        setError(null);
        const result = await dashboardApi.startConversation(agentIds, topic);
        // Optimistically update conversations
        setConversations((prev) => [...prev, result.conversation]);
        return result.conversation;
      } catch (err) {
        setError(
          err instanceof Error ? err.message : "Failed to start conversation",
        );
        throw err;
      }
    },
    [],
  );

  const endConversation = useCallback(async (conversationId: string) => {
    try {
      setError(null);
      await dashboardApi.endConversation(conversationId);
      // Optimistically update conversations
      setConversations((prev) =>
        prev.map((conv) =>
          conv.id === conversationId
            ? { ...conv, status: "completed" as const }
            : conv,
        ),
      );
    } catch (err) {
      setError(
        err instanceof Error ? err.message : "Failed to end conversation",
      );
      throw err;
    }
  }, []);

  useEffect(() => {
    fetchConversations();
  }, [fetchConversations]);

  return {
    conversations,
    loading,
    error,
    refetch: fetchConversations,
    startConversation,
    endConversation,
  };
}

// Hook for system status
export function useSystemStatus() {
  const [status, setStatus] = useState<SystemStatus | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchStatus = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const result = await dashboardApi.getSystemStatus();
      setStatus(result);
    } catch (err) {
      setError(
        err instanceof Error ? err.message : "Failed to fetch system status",
      );
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchStatus();
    // Refresh status every 10 seconds
    const interval = setInterval(fetchStatus, 10000);
    return () => clearInterval(interval);
  }, [fetchStatus]);

  return { status, loading, error, refetch: fetchStatus };
}

// Hook for simulation control
export function useSimulationControl() {
  const { isSimulationRunning, setSimulationRunning } = useDashboardStore();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const toggleSimulation = useCallback(
    async (running: boolean) => {
      try {
        setLoading(true);
        setError(null);
        // Optimistically update store
        setSimulationRunning(running);
        await dashboardApi.toggleSimulation(running);
      } catch (err) {
        // Revert optimistic update on error
        setSimulationRunning(!running);
        setError(
          err instanceof Error ? err.message : "Failed to toggle simulation",
        );
        throw err;
      } finally {
        setLoading(false);
      }
    },
    [setSimulationRunning],
  );

  return {
    isSimulationRunning,
    loading,
    error,
    toggleSimulation,
  };
}

// Hook for dashboard state export/import
export function useDashboardState() {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const exportState = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const result = await dashboardApi.exportState();
      // Trigger download
      window.open(result.exportUrl, "_blank");
      return result.exportUrl;
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to export state");
      throw err;
    } finally {
      setLoading(false);
    }
  }, []);

  const importState = useCallback(async (stateData: any) => {
    try {
      setLoading(true);
      setError(null);
      await dashboardApi.importState(stateData);
      // Refresh all data after import
      window.location.reload();
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to import state");
      throw err;
    } finally {
      setLoading(false);
    }
  }, []);

  return {
    loading,
    error,
    exportState,
    importState,
  };
}

// Combined hook for dashboard data
export function useDashboard() {
  const metrics = useDashboardMetrics();
  const agents = useAgents();
  const conversations = useConversations();
  const systemStatus = useSystemStatus();
  const simulation = useSimulationControl();
  const state = useDashboardState();

  return {
    metrics,
    agents,
    conversations,
    systemStatus,
    simulation,
    state,
  };
}
